@font-face {
  font-family: "Rurik (viking)";
  src: url("Rurikviking-Regular.eot");
  src: url("Rurikviking-Regular.eot?#iefix") format("embedded-opentype"), url("Rurikviking-Regular.woff2") format("woff2"), url("Rurikviking-Regular.woff") format("woff"), url("Rurikviking-Regular.ttf") format("truetype"), url("Rurikviking-Regular.svg#Rurikviking-Regular") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
.Footer .footer_menu img {
  -webkit-transition: 500ms ease-in; /* Safari and Chrome */
  -moz-transition: 500ms ease-in; /* Firefox 4 */
  -o-transition: 500ms ease-in;
}

.header .head_menu button {
  width: 100px;
  height: 42px;
  border-left: solid 2px rgba(255, 255, 255, 0.656);
  border-right: solid 2px rgba(255, 255, 255, 0.656);
  border-top: none;
  border-bottom: none;
  background: rgba(0, 12, 245, 0);
  color: #ffffff;
  padding: auto;
  font-size: 20px;
  position: relative;
  transition: all 250ms ease;
  font-family: "Rurik (viking)";
}
.header .head_menu button:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 42px;
  background: rgba(255, 255, 255, 0.407);
  transition: all 1000ms ease;
}
.header .head_menu button:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  height: 42px;
  background: rgba(255, 255, 255, 0.407);
  transition: all 1000ms ease;
  font-family: ff_head;
}
.header .head_menu button:hover:before, .header .head_menu .btn button:hover::after {
  width: 50%;
}
.header .head_menu button:hover {
  border: none rgba(255, 0, 0, 0);
  transition: all 250ms ease;
}

:root {
  --h1-fsz:25px;
  --h2-fsz:18px;
  --h3-fsz:16px;
  --p-fsz:14px;
  --bh-color:rgba(149, 147, 147, 0.49);
  --f-color:white;
}

body {
  background-size: cover;
  background-repeat: no-repeat;
  display: grid;
  width: 100vw;
  height: 100vh;
  place-content: center;
  background-color: black;
}

.container {
  background-size: cover;
  background-repeat: no-repeat;
  display: grid;
  width: 100vw;
  height: 100vh;
  place-content: center;
  background-image: url("https://kartinkin.net/uploads/posts/2022-03/1646467657_5-kartinkin-net-p-kartinki-shaman-5.jpg");
}

.header {
  display: grid;
  grid-template-columns: 0.5fr 4fr;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100px;
  z-index: 10;
  font-size: 10px;
  color: black;
  background-color: rgba(91, 91, 91, 0.422);
  font-family: ff_head;
}
.header .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100px;
}
.header .logo img {
  width: 100px;
  height: 100px;
}
.header .head_menu {
  font-family: ff_head;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
}

.Body {
  position: fixed;
  top: 100px;
  display: grid;
  width: 100vw;
  min-height: calc(100vh - 100px - 50px);
  font-family: "Rurik (viking)";
}
.Body .BodyContainer {
  position: fixed;
  display: grid;
  width: 100vw;
  min-height: calc(100vh - 100px - 50px);
  font-family: "Rurik (viking)";
}
.Body .BodyContainer .containerText {
  text-align: center;
  text-shadow: 1px 1px 1px #000;
}
.Body .BodyContainer .containerText h1 {
  font-size: 70px;
  color: white;
  font-family: "Rurik (viking)";
}

.Footer {
  display: flex;
  justify-content: space-between;
  color: white;
  width: 100vw;
  height: 50px;
  bottom: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0);
}
.Footer .footer_menu {
  border: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: auto;
  width: 100px;
  height: 100%;
}
.Footer .footer_menu h1 {
  font-size: 15px;
}
.Footer .footer_menu a {
  text-decoration: none;
}
.Footer .footer_menu span {
  font-size: 10px;
  font-style: italic;
  cursor: pointer;
  text-decoration: none;
  color: white;
}
.Footer .footer_menu img {
  margin: auto;
  width: 40px;
  height: 40px;
  filter: brightness(80%);
}
.Footer .footer_menu img:hover {
  width: 45px;
  height: 45px;
  filter: brightness(120%);
}

@media (max-width: 500px) {
  .header .logo .logoImg {
    display: flex;
    justify-content: flex-start;
    width: 50px;
  }
  .header .head_menu {
    display: flex;
    flex-wrap: wrap;
  }
  .header .head_menu button {
    font-size: 15px;
    width: -moz-fit-content;
    width: fit-content;
    height: 30px;
  }
}/*# sourceMappingURL=style.css.map */